import {
  Button,
  Chip,
  CircularProgress,
  Container,
  Dialog,
  DialogActions,
  DialogTitle,
} from "@material-ui/core";
import React, { useEffect, useState } from "react";
import Contact from "../../../components/guest/contact/Contact";
import "./sellerOrderDetail.scss";
import { useDispatch, useSelector } from "react-redux";
import {
  acceptOrder,
  fetchContractDetail,
  fetchContracts,
  rejectOrder,
  selectContractDetail,
  selectContractStatus,
} from "../../../redux/contractSlice";
import { useNavigate, useParams } from "react-router-dom";
import Alert from "@material-ui/lab/Alert";
import SellerHeader from "../../../components/seller/sellerHeader/SellerHeader";

export default function SellerOrderDetail() {
  const { orderId } = useParams();
  const contractDetail = useSelector(selectContractDetail);
  useEffect(() => {
    dispatch(fetchContractDetail(orderId));
  }, []);

  const status = useSelector(selectContractStatus);
  const [error, setError] = useState("");
  const [success, setSuccess] = useState("");
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const handleAcceptOrder = (e) => {
    e.preventDefault();
    dispatch(acceptOrder(orderId))
      .unwrap()
      .then(() => {
        dispatch(fetchContracts());
        setSuccess("Duyt n thanh cng!");
        navigate("/sellerHome/manageContract", {
          state: {
            alert: "Duyt n thanh cng!",
          },
        });
      })
      .catch(() => {
        setError("Duyt n tht bai!");
      });
  };
  const handleRejectOrder = (e) => {
    e.preventDefault();
    setOpenDelete(false);
    dispatch(rejectOrder(orderId))
      .unwrap()
      .then(() => {
        dispatch(fetchContracts());
        navigate("/sellerHome/manageOrder", {
          state: {
            alert: "T chi n thanh cng!",
          },
        });
        setSuccess("T chi n thanh cng!");
      })
      .catch(() => {
        setError("T chi n tht bai!");
      });
  };
  const [openDelete, setOpenDelete] = React.useState(false);
  const handleClickOpenDelete = () => {
    setOpenDelete(true);
  };

  const handleCloseDelete = () => {
    setOpenDelete(false);
  };
  return (
    <div className="contract-details">
      <SellerHeader />
      <h1 className="contract-title">Chi tit n hang</h1>
      <Container maxWidth="lg" className="contract-form">
        <div className="contract-id">
          <h2>Ma n hang : {contractDetail.contractCode} </h2>
          <Chip
            label={
              contractDetail.orderStatus == "PENDING" ? "ang ch" : "a huy"
            }
            className="chip_pending"
          />
        </div>
        <div className="contract-content-require">
          <h3>Yu cu:</h3>
          <p id="require">{contractDetail.requirement}</p>
        </div>
        <div className="contract-content-time">
          <h3>Tng thi gian ban giao:</h3>
          <p>{contractDetail.totalDeliveryTime} ngay</p>
        </div>
        <div className="contract-expect-date">
          <h3>Ngay hoan thanh d kin:</h3>
          <p>{contractDetail.expectCompleteDate}</p>
        </div>
        <div className="contract-quantity">
          <h4>S lng : </h4>
          <p>{contractDetail.quantity}</p>
        </div>
        <div className="contract-quantity">
          <h4>Tng chi phi : </h4>
          <p>{contractDetail.totalPrice} $</p>
        </div>
        <div className="contract-cancel-fee">
          <h4>Phi huy hp ng : </h4>
          <p>
            {contractDetail.contractCancelFee}% ( =
            {(
              (contractDetail.totalPrice *
                contractDetail.quantity *
                contractDetail.contractCancelFee) /
              100
            ).toLocaleString()}{" "}
            $ )
          </p>
        </div>
        <div className="contract-button">
          {contractDetail.orderStatus == "PENDING" ? (
            <>
              {" "}
              <Button
                variant="contained"
                color="primary"
                style={{ marginRight: "30px" }}
                onClick={handleAcceptOrder}
              >
                Duyt n
              </Button>
              <Button
                variant="contained"
                color="secondary"
                onClick={handleClickOpenDelete}
              >
                T chi
              </Button>
            </>
          ) : (
            <Button variant="outlined" style={{ color: "gray" }} disabled>
              n hang a huy
            </Button>
          )}
        </div>{" "}
        {status == "loading" && (
          <CircularProgress style={{ margin: "0 auto" }} />
        )}
      </Container>
      <Dialog
        open={openDelete}
        onClose={handleCloseDelete}
        aria-labelledby="responsive-dialog-title"
      >
        <DialogTitle id="responsive-dialog-title">
          {"Ban co mun t chi yu cu nay?"}
        </DialogTitle>
        <DialogActions>
          <Button
            onClick={handleRejectOrder}
            color="secondary"
            variant="outlined"
          >
            T chi
          </Button>
          <Button
            onClick={handleCloseDelete}
            color="default"
            variant="outlined"
          >
            Huy
          </Button>
        </DialogActions>
      </Dialog>

      {error && <Alert severity="error">{error}</Alert>}
      {success && <Alert severity="success">{success}</Alert>}
      <div className="sections_profile">
        <Contact />
      </div>
    </div>
  );
}
